import React from 'react';
import { data } from '../../data/knowledge-base/chinese/pinyin.js';
import './test-shengmuyunmu.css';

const TestShengmuYunmu = () => {
  // 
  const shengmu = data.find(item => item.title === '');
  const danYunmu = data.find(item => item.title === '');
  const fuYunmu = data.find(item => item.title === '');
  const qianBiYunmu = data.find(item => item.title === '');
  const houBiYunmu = data.find(item => item.title === '');

  const renderPinyinCard = (item) => (
    <div className="pinyin-card" key={item.d1}>
      <div className="pinyin">{item.d1}</div>
      <div className="example">{item.d2}</div>
      {item.d3 && <div className="category">{item.d3}</div>}
    </div>
  );

  const renderSection = (title, items) => (
    <div className="section">
      <h2>{title}</h2>
      <div className="cards-container">
        {items.data.map(renderPinyinCard)}
      </div>
    </div>
  );

  return (
    <div className="test-shengmu-yunmu">
      
      {shengmu && renderSection('', shengmu)}
      
      <div className="yunmu-container">
        {danYunmu && renderSection('', danYunmu)}
        {fuYunmu && renderSection('', fuYunmu)}
        {qianBiYunmu && renderSection('', qianBiYunmu)}
        {houBiYunmu && renderSection('', houBiYunmu)}
      </div>
    </div>
  );
};

export default TestShengmuYunmu;
