import React, { useState, useEffect } from 'react';
import './transcript.css';

const Transcript = ({ studentId, subject }) => {
  const [testData, setTestData] = useState([]);
  const [stats, setStats] = useState({
    totalQuestions: 0,
    totalCorrect: 0,
    totalWrong: 0,
    accuracy: 0,
    wrongCharacters: [],
    characterPerformance: {}
  });
  const [loading, setLoading] = useState(true);

  // 
  useEffect(() => {
    const loadTestData = async () => {
      try {
        // 
        const filePrefix = {
          '': 'hanzi',
        }[subject];

        if (!filePrefix) {
          console.error(':', subject);
          setLoading(false);
          return;
        }

        // 
        try {
          // 
          const data1 = await import(`../../data/user-data/${studentId}/${filePrefix}-2025-07-01.json`);
          const data2 = await import(`../../data/user-data/${studentId}/${filePrefix}-2025-07-02.json`).catch(() => null);

          const tests = [];
          
          // 
          tests.push({
            date: '2025-07-01',
            data: data1.default,
            displayName: '202571'
          });

          // 
          if (data2) {
            tests.push({
              date: '2025-07-02',
              data: data2.default,
              displayName: '202572'
            });
          }

          // 
          const validTests = tests.sort((a, b) => new Date(b.date) - new Date(a.date));

          setTestData(validTests);
          if (validTests.length > 0) {
            calculateStats(validTests);
          } else {
            setStats({
              totalQuestions: 0,
              totalCorrect: 0,
              totalWrong: 0,
              accuracy: 0,
              wrongCharacters: [],
              characterPerformance: {}
            });
          }
        } catch (error) {
          console.error(':', error);
          setTestData([]);
          setStats({
            totalQuestions: 0,
            totalCorrect: 0,
            totalWrong: 0,
            accuracy: 0,
            wrongCharacters: [],
            characterPerformance: {}
          });
        }
        setLoading(false);
      } catch (error) {
        console.error(':', error);
        setLoading(false);
      }
    };

    if (studentId && subject) {
      loadTestData();
    }
  }, [studentId, subject]);

  // 
  const calculateStats = (tests) => {
    let totalQuestions = 0;
    let totalCorrect = 0;
    let wrongCharacters = new Set();
    let characterPerformance = {};

    tests.forEach(test => {
      if (!test.data || !Array.isArray(test.data)) {
        console.error('Invalid test data format:', test);
        return;
      }

      test.data.forEach(item => {
        if (!item || typeof item.t1 === 'undefined' || !item.d1) {
          console.error('Invalid test item format:', item);
          return;
        }

        totalQuestions++;
        if (item.t1) {
          totalCorrect++;
        } else {
          wrongCharacters.add(item.d1);
        }

        // 
        if (!characterPerformance[item.d1]) {
          characterPerformance[item.d1] = { correct: 0, wrong: 0, total: 0 };
        }
        characterPerformance[item.d1].total++;
        if (item.t1) {
          characterPerformance[item.d1].correct++;
        } else {
          characterPerformance[item.d1].wrong++;
        }
      });
    });

    const accuracy = totalQuestions > 0 ? (totalCorrect / totalQuestions * 100).toFixed(1) : 0;

    setStats({
      totalQuestions,
      totalCorrect,
      totalWrong: totalQuestions - totalCorrect,
      accuracy,
      wrongCharacters: Array.from(wrongCharacters),
      characterPerformance
    });
  };

  // 
  const getCharacterGrade = (char) => {
    const performance = stats.characterPerformance[char];
    if (!performance) return 'unknown';
    
    const accuracy = performance.correct / performance.total;
    if (accuracy >= 0.9) return 'excellent';
    if (accuracy >= 0.7) return 'good';
    if (accuracy >= 0.5) return 'average';
    return 'poor';
  };

  // 
  const getGradeColor = (grade) => {
    switch (grade) {
      case 'excellent': return '#4CAF50';
      case 'good': return '#8BC34A';
      case 'average': return '#FFC107';
      case 'poor': return '#F44336';
      default: return '#9E9E9E';
    }
  };

  // 
  const getStudyAdvice = () => {
    if (stats.accuracy >= 90) {
      return '';
    } else if (stats.accuracy >= 80) {
      return '';
    } else if (stats.accuracy >= 70) {
      return '';
    } else {
      return '';
    }
  };

  if (loading) {
    return <div className="transcript-loading">...</div>;
  }

  return (
    <div className="transcript-container">
      <div className="transcript-header">
        <h1>{subject}</h1>
        <p className="student-info">{studentId}</p>
      </div>

      {testData.length === 0 ? (
        <div className="no-data">
          <p>{subject}</p>
        </div>
      ) : (
        <>
          {/*  */}
          <div className="overall-stats">
            <h2></h2>
            <div className="stats-grid">
              <div className="stat-item">
                <div className="stat-value">{stats.totalQuestions}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-value">{stats.totalCorrect}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-value">{stats.totalWrong}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-value">{stats.accuracy}%</div>
                <div className="stat-label"></div>
              </div>
            </div>
          </div>

          {/*  */}
          <div className="character-performance">
            <h2></h2>
            <div className="performance-summary">
              <p></p>
            </div>
            
            {/*  */}
            <div className="characters-performance-grid">
              {Object.entries(stats.characterPerformance || {})
                .sort((a, b) => {
                  // 
                  const accuracyA = a[1].correct / a[1].total;
                  const accuracyB = b[1].correct / b[1].total;
                  return accuracyA - accuracyB;
                })
                .map(([char, performance]) => {
                  const accuracy = ((performance.correct / performance.total) * 100).toFixed(1);
                  const isWeak = performance.correct / performance.total < 0.7;
                  
                  return (
                    <div key={char} className={`character-card ${isWeak ? 'weak-performance' : 'good-performance'}`}>
                      <div className="character-main">
                        <span className="character-text">{char}</span>
                      </div>
                      <div className="character-stats">
                        <div className="stat-row">
                          <span className="stat-label"></span>
                          <span className="stat-value">{performance.total}</span>
                        </div>
                        <div className="stat-row">
                          <span className="stat-label correct"></span>
                          <span className="stat-value correct">{performance.correct}</span>
                        </div>
                        <div className="stat-row">
                          <span className="stat-label wrong"></span>
                          <span className="stat-value wrong">{performance.wrong}</span>
                        </div>
                        <div className="accuracy-display">
                          <span className={`accuracy-badge ${isWeak ? 'low' : 'high'}`}>
                            {accuracy}%
                          </span>
                        </div>
                      </div>
                    </div>
                  );
                })}
            </div>
            
            {/*  */}
            <div className="test-dates-summary">
              <h3></h3>
              <div className="dates-grid">
                {testData.map((test, index) => {
                  const correct = test.data.filter(item => item.t1).length;
                  const total = test.data.length;
                  const accuracy = ((correct / total) * 100).toFixed(1);
                  
                  return (
                    <div key={test.date} className="date-summary-card">
                      <div className="date-title">{test.displayName}</div>
                      <div className="date-stats">
                        <div className="stat-item">
                          <div className="stat-value">{total}</div>
                          <div className="stat-label"></div>
                        </div>
                        <div className="stat-item">
                          <div className="stat-value">{accuracy}%</div>
                          <div className="stat-label"></div>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>

          {/*  */}
          <div className="wrong-characters">
            <h2></h2>
            <div className="wrong-chars-grid">
              {stats.wrongCharacters?.map((char, index) => (
                <div key={index} className="wrong-char-item">
                  <span className="wrong-char">{char}</span>
                  <div className="char-stats">
                     {stats.characterPerformance[char]?.wrong || 0} 
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/*  */}
          <div className="study-advice">
            <h2></h2>
            <div className="advice-content">
              <p>{getStudyAdvice()}</p>
              <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          </div>

          {/*  */}
          <div className="progress-chart">
            <h2></h2>
            <div className="progress-bar">
              <div 
                className="progress-fill" 
                style={{ width: `${stats.accuracy}%` }}
              >
                {stats.accuracy}%
              </div>
            </div>
            <div className="progress-labels">
              <span>0%</span>
              <span>50%</span>
              <span>100%</span>
            </div>
          </div>

          {/*  */}
          <div className="transcript-footer">
            <p>{new Date().toLocaleString('zh-CN')}</p>
          </div>
        </>
      )}
    </div>
  );
};

export default Transcript;
